
.team-1.fdb-block {
  img {
    border-bottom: solid 5px $primary;
    @include border-radius(0 !important);
  }

  .fdb-box {
    padding: 0;

    .content {
      @include rem(padding, 20px);
    }
  }
}

.team-2.fdb-block {
  img {
    @include border-radius(100% !important);
  }
}

.team-3.fdb-block {
  img {
    @include border-radius(100% !important);
    @include froala-box-shadow(1);
  }
}
